import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { KarigarLedgerService } from "../../service/karigarLedger.service";
import { KarigarService } from "../../service/karigar.service";
@Component({
  selector: "app-salary-ledger",
  templateUrl: "./salary-ledger.component.html",
  styleUrls: ["./salary-ledger.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class SalaryFormComponent implements OnInit {
  public contentHeader: Object;
  public salaryLedgerForm: FormGroup;
  public getKarigar;
  public tableDataArray = [];
  public paymentTypeArray = ["Advance", "Salary","Loan","Miscellaneous"];
  public submitted = false;
  public editable = false;
  public id;
  public karigar = {
    name: null,
    number: null,
    designation: null,
  };

  constructor(
    private _sw: SweetalertComponent,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private _karigar: KarigarService,
    private _serviceData: KarigarLedgerService,
    private _router: Router
  ) {}

  getAllKarigar() {
    this._karigar.getAll().subscribe((res) => {
      if (res.data) {
        this.getKarigar = res.data;
      }
    });
  }
  selectedKarigar(value) {
    this.karigar.name = value.name;
    this.karigar.number = value.phone;
    this.karigar.designation = value.designation;
  }

  onSubmit(formData) {

    this.submitted = true;
    if (this.salaryLedgerForm.invalid) {
      return;
    }

    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    this._serviceData.create(formData).subscribe(
      (res) => {
        this._sw.fire("success", "Karigar Ledger", `Record Successfully Add..`);
        this.submitted = false;
        this.salaryLedgerForm.reset();
        this.getAllDetail()
      },
      (err) => {
        this._sw.fire("error", "Karigar Ledger", err.error.message);
      }
    );
  }

  updateFormDetail(formData) {
    this._serviceData.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire(
          "success",
          "Karigar Ledger",
          `Record Successfully Update..`
        );
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/karigar/ledger/salary"]);
      },
      (err) => {
        this._sw.fire("error", "Karigar Ledger", err.error.message);
      }
    );
  }

  ngOnInit(): void {
    this.getAllKarigar();
    this.getAllDetail()
    this.id = +this.route.snapshot.paramMap.get("id");
    if (this.id > 0) {
      this.getDetailById();
    }
    this.makeReactiveForm();

    this.contentHeader = {
      headerTitle: "Karigar Ledger",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Karigar",
            isLink: true,
            link: "/",
          },
          {
            name: "Karigar Ledger",
            isLink: false,
          },
        ],
      },
    };
  }

  getDetailById() {
    this._serviceData.getDetailById({ id: this.id }).subscribe((res) => {
      this.editable = true;
      this.salaryLedgerForm.patchValue({
        karigarName: res.data.karigarName,
        karigarMobile: res.data.karigarMobile,
        karigarDesignation: res.data.karigarDesignation,
        paymentType: res.data.paymentType,
        toDate: res.data.toDate,
        amount: res.data.amount,
        remarks: res.data.remarks,
      });
    });
  }

  get f() {
    return this.salaryLedgerForm.controls;
  }

  makeReactiveForm() {
    this.salaryLedgerForm = this.fb.group({
      karigarName: [null, [Validators.required]],
      karigarMobile: [""],
      karigarDesignation: [null],
      paymentType: [null, [Validators.required]],
      toDate: ["", [Validators.required]],
      amount: ["", [Validators.required]],
      remarks: [null],
    });
  }

  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data && this.tableDataArray.length == 1) {
        this.tableDataArray = [];
      }
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }

  searchTerm: string = null;
  get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }
    return this.tableDataArray.filter((item) =>
      item.karigarName.toLowerCase().includes(this.searchTerm.toLowerCase())
    );
  }
}
