import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { CrudComponent } from "./components/crud/crud.component";
import { RouterModule, Routes } from "@angular/router";
import { ContentHeaderModule } from "app/layout/components/content-header/content-header.module";
import { CoreCommonModule } from "@core/common.module";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { NgSelectModule } from "@ng-select/ng-select";
import { SalaryFormComponent } from "./components/salary/salary-ledger.component";


const routes: Routes = [
  {
    path: "",
    component: CrudComponent,
  },
  {
    path: ":id",
    component: CrudComponent,
  },
  {
    path: "ledger/salary",
    component: SalaryFormComponent,
  },
  {
    path: "ledger/salary/:id",
    component: SalaryFormComponent,
  },
];

@NgModule({
  declarations: [CrudComponent,SalaryFormComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ContentHeaderModule,
    CoreCommonModule,
    FormsModule,
    ReactiveFormsModule,
    NgSelectModule,
  ],
  providers: [SweetalertComponent],
})
export class KarigarModule {}
