import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { KarigarService } from "../../service/karigar.service";

@Component({
  selector: "app-crud",
  templateUrl: "./crud.component.html",
  styleUrls: ["./crud.component.scss"],
})
export class CrudComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];
  public karigarFrom: FormGroup;
  public submitted = false;
  public editable = false;
  public id;
  public avatarImage: string;
  public imageName;

  constructor(
    private _sw: SweetalertComponent,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private _serviceData: KarigarService,
    private _router: Router
  ) {}

  onSubmit(formData) {
    this.submitted = true;
    if (this.karigarFrom.invalid) {
      return;
    }
    this.imageName =
      this.avatarImage == undefined ? undefined : `${Date.now()}.png`;
    let imageUpload = {
      file: this.avatarImage,
      imageName: this.imageName,
      filePath: "karigar/",
    };

    if (this.avatarImage !== undefined) {
      this._serviceData.uploadImage(imageUpload).subscribe();
    }
    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    this._serviceData
      .create({
        ...formData,
        karigar_image: this.imageName,
      })
      .subscribe(
        (res) => {
          this._sw.fire("success", "Karigar", `Record Successfully Add..`);
          this.getAllDetail();
          this.submitted = false;
          this.karigarFrom.reset();
        },
        (err) => {
          this._sw.fire("error", "Karigar", err.error.message);
        }
      );
  }

  updateFormDetail(formData) {
    this._serviceData.update({ id: this.id, ...formData,karigar_image: this.imageName}).subscribe(
      (res) => {
        this._sw.fire("success", "Karigar", `Record Successfully Update..`);
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/karigar"]);
      },
      (err) => {
        this._sw.fire("error", "Karigar", err.error.message);
      }
    );
  }

  ngOnInit(): void {
    this.id = +this.route.snapshot.paramMap.get("id");

    if (this.id > 0) {
      this.getDetailById();
    }
    this.getAllDetail();
    this.makeReactiveForm();

    this.contentHeader = {
      headerTitle: "Karigar",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Karigar",
            isLink: true,
            link: "/",
          },
          {
            name: "Information",
            isLink: false,
          },
        ],
      },
    };
  }
  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      }
    });
  }

  get f() {
    return this.karigarFrom.controls;
  }

  getDetailById() {
    this._serviceData.getDetailById({ id: this.id }).subscribe((res) => {
      this.editable = true;
      this.karigarFrom.patchValue({
        status: res.data.status,
        name: res.data.name,
        phone: res.data.phone,
        cnic: res.data.cnic,
        joinDate: res.data.joinDate,
        address: res.data.address,
        karigar_image: res.data.karigar_image,
        designation:res.data.designation
      });
      
      this.avatarImage =
        res.data.karigar_image == null
          ? undefined
          : `assets/images/karigar/${res.data.karigar_image}`;
    });
  }

  makeReactiveForm() {
    this.karigarFrom = this.fb.group({
      karigar_image: [],
      name: ["", [Validators.required]],
      phone: ["", [Validators.required]],
      cnic: ["", [Validators.required]],
      joinDate: [""],
      designation:["", [Validators.required]],
      address: [null],
      status: [],
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }

  /**
   * Upload Image
   *
   * @param event
   */
  uploadImage(event: any) {
    if (event.target.files && event.target.files[0]) {
      let reader = new FileReader();

      reader.onload = (event: any) => {
        this.avatarImage = event.target.result;
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  searchTerm: string = '';
 get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }
    return this.tableDataArray.filter(item => item.name.toLowerCase().includes(this.searchTerm.toLowerCase()));
  }
}
